import axios from "axios";
export default {
  name: "zq-single-task",
  data() {
    return {
      del: false,
      detailedInfo: false,
      shareShow: false,
      minutes: 60,
      origin: undefined
    };
  },
  props: {
    size: Number,
    per: Number,
    taskName: String,
    downStatus: String,
    uuid: String,
    url: String,
    sha256: String,
    shared: Boolean
  },
  methods: {
    deleteTask() {
      axios.get("api/request-cancel?uuid=" + this.uuid);
      this.del = true;
    },
    downTask() {
      window.open("api/fetch?uuid=" + this.uuid);
    },
    shareTask() {
      this.shareShow = true;
    },
    makeShare() {
      axios.get("api/share?uuid=" + this.uuid + '&minutes=' + this.minutes);
    }
  },
  mounted() {
    this.origin = window.location.origin;
  }
};