import axios from "axios";
export default {
  name: "zqChangePasswordDialog",
  data() {
    return {
      old: '',
      newPassword: ''
    };
  },
  mounted() {},
  methods: {
    submit() {
      this.doCallbackAlert('abc');
      axios.post("api/user/change-password", {
        old_password: this.old,
        new_password: this.newPassword
      }).then(r => {
        if (r.data.success === '0') alert("OK");else alert("failed");
      });
    }
  }
};