import { ref } from "vue";
export default {
  name: "zqDirectDialog",
  data() {
    return {
      dialogLoading: false,
      requestDownUrl: ref('')
    };
  },
  mounted() {},
  inject: ['dismissDialog'],
  methods: {
    requestDown() {
      window.open("api/direct-connect?url=" + encodeURI(this.requestDownUrl.toString()));
      this.dismissDialog();
    }
  }
};