import axios from "axios";
export default {
  name: "zqAddUserDialog",
  data() {
    return {
      password: '',
      account: ''
    };
  },
  mounted() {},
  methods: {
    register() {
      axios.post("api/user/register", {
        username: this.account.trim(),
        password: this.password
      }).then(r => {
        if (r.data.success === '0') alert('OK');else alert("Failed");
      });
    }
  }
};